@import url('https://fonts.googleapis.com/css?family=Cuprum');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300');

html {
  height: 100%;
  background:
                -webkit-linear-gradient(140deg, rgba(111, 0, 41,.8),  rgba(196, 158, 103,.8)),
                -webkit-radial-gradient(rgba(196, 158, 103,.7), rgba(111, 0, 41,.7)), fixed,
                url("../ULM.JPG")
                ;
  background:
                -o-linear-gradient(140deg, rgba(111, 0, 41,.8),  rgba(196, 158, 103,.8)),
                -o-radial-gradient(rgba(196, 158, 103,.7), rgba(111, 0, 41,.7)), fixed,
                url("../ULM.JPG")
                ;
  background:
                linear-gradient(-50deg, rgba(111, 0, 41,.8),  rgba(196, 158, 103,.8)),
                radial-gradient(rgba(196, 158, 103,.7), rgba(111, 0, 41,.7)), fixed,
                url("../ULM.JPG")
                ;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

body {
  height: 100%;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;

}

#root {
    height: 100%;
    font-family: 'Open Sans';
}

button {
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;

}

button:hover {
    background-color: rgba(0,0,0, .8);
    -webkit-transition: background-color .3s linear;
    -o-transition: background-color .3s linear;
    transition: background-color .3s linear;
    -webkit-transition-property: background-color;
    -o-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: linear;
         -o-transition-timing-function: linear;
            transition-timing-function: linear;
    -webkit-transition-delay: initial;
         -o-transition-delay: initial;
            transition-delay: initial;
}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  text-transform: uppercase;

}

a:hover{
    color: #C2939C;
    -webkit-transition: color .3s linear;
    -o-transition: color .3s linear;
    transition: color .3s linear;
    -webkit-transition-property: color;
    -o-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: linear;
         -o-transition-timing-function: linear;
            transition-timing-function: linear;
    -webkit-transition-delay: initial;
         -o-transition-delay: initial;
            transition-delay: initial;
}

input {
    font-family: 'Open Sans';
}

#App {
  height: 100%;
  font-family: 'Open Sans';
}



